import React, { Component, Fragment } from "react";
import {
  Pagination, PaginationItem, PaginationLink,
  UncontrolledDropdown, DropdownMenu, DropdownToggle, DropdownItem
} from "reactstrap";

export default class ProductListPagination extends Component {
  constructor(props) {
    super(props);

    this.getSafePage = this.getSafePage.bind(this);
    this.changePage = this.changePage.bind(this);
    this.applyPage = this.applyPage.bind(this);
    this.pageClick = this.pageClick.bind(this);
    this.renderPages = this.renderPages.bind(this);
    this.changePageSize = this.changePageSize.bind(this);
    this.renderPageJump = this.renderPageJump.bind(this);

    this.state = {
      page: props.page,
      pageSize: this.props.defaultPageSize
    };
  }

  static getDerivedStateFromProps(props, state) {
    return {page: props.page};
  }

  getSafePage(page) {
    if (Number.isNaN(page)) {
      page = this.props.page;
    }
    return page;
    // return Math.min(Math.max(page, 0), this.props.pages - 1);
  }

  changePageSize(size) {
    this.props.onPageSizeChange(size);
    this.setState({ pageSize: size });
  }

  changePage(page) {
    page = this.getSafePage(page);
    this.setState({ page });
    if (this.props.page !== page) {
      this.props.onPageChange(page);
    }
  }

  applyPage(e) {
    if (e) {
      e.preventDefault();
    }
    const page = this.state.page;
    this.changePage(page === "" ? this.props.page : page);
  }

  pageClick(pageIndex) {
    this.changePage(pageIndex);
  }

  renderPages() {
    // let pageCount = this.props.pages;
    const { pages } = this.props;
    const { page: statePage } = this.state;
    let pageButtons = [];
    pages.forEach( (page) => {
      if(page !== 0) {
        let active = statePage === page ? true : false;
        pageButtons.push(
          <PaginationItem key={page} active={active}>
            <PaginationLink
              onClick={() => this.pageClick(page)}
            >{page}</PaginationLink>
          </PaginationItem>
        );
      } else {
        pageButtons.push(
          <PaginationItem className="pointer-events-none" key={Math.random().toString(36).substring(7)}>
            <PaginationLink>...</PaginationLink>
          </PaginationItem>
        );
      }
    });
    // for (let i = 0; i < pageCount.length; i++) {
    //   let active = this.state.page === i ? true : false;
    //   pageButtons.push(
    //     <PaginationItem key={i} active={active}>
    //       <PaginationLink
    //         onClick={() => this.pageClick(i)}
    //       >{i + 1}</PaginationLink>
    //     </PaginationItem>
    //   );
    // }
    return pageButtons;
  }

  renderPageJump() {
    let pages = this.props.pages;
    let pageNumbers = [];
    for (let i = 0; i < pages; i++) {
      pageNumbers.push(
        <DropdownItem
          key={i}
          onClick={() => this.changePage(i)}
        >
          {i + 1}
        </DropdownItem>
      );
    }
    return pageNumbers;
  }

  render() {
    const {
      page,
      pages,
      canPrevious,
      canNext,
      pageSizeOptions,
      showPageSizeOptions,
      showPageJump,
      totalResults
    } = this.props;

    return (
      <Fragment>
        <div className="text-center mt-5">
          {
            showPageJump &&
            <div className="float-left pt-2"><span>Page </span>
              <UncontrolledDropdown className="d-inline-block">
                <DropdownToggle caret color="outline-primary" size="xs">
                  {this.state.page}
                </DropdownToggle>
                <DropdownMenu direction="left" >
                  {this.renderPageJump()}
                </DropdownMenu>
              </UncontrolledDropdown>
              <span> of </span>{pages}</div>
          }

          <Pagination className="d-inline-block" size="sm" listClassName="justify-content-center" aria-label="Page navigation example">
            <PaginationItem className={`${ (!canPrevious || (page < 2)) && "disabled"}`}>
              <PaginationLink
                className={"prev"}
                onClick={() => {
                  if (!canPrevious) return;
                  this.changePage(1);
                }}
                disabled={!canPrevious}>
                <i className="simple-icon-control-start" />
              </PaginationLink>
            </PaginationItem>

            <PaginationItem className={`${ (!canPrevious || (page < 2)) && "disabled"}`}>
              <PaginationLink
                className={"prev"}
                onClick={() => {
                  if (!canPrevious) return;
                  this.changePage(page - 1);
                }}
                disabled={!canPrevious}>
                <i className="simple-icon-arrow-left" />
              </PaginationLink>
            </PaginationItem>

            {this.renderPages()}

            <PaginationItem className={`${ (!canNext || (totalResults === 0) || (page === Math.max(...pages))) && "disabled"}`}>
              <PaginationLink
                className="next"
                onClick={() => {
                  if (!canNext) return;
                  this.changePage(page);
                }}
                disabled={!canNext}>
                <i className="simple-icon-arrow-right" />
              </PaginationLink>
            </PaginationItem>


            <PaginationItem className={`${ (!canNext || (totalResults === 0) || (page === Math.max(...pages))) && "disabled"}`}>
              <PaginationLink
                className="next"
                onClick={() => {
                  if (!canNext) return;
                  this.changePage(Math.max(...pages));
                }}
                disabled={!canNext}>
                <i className="simple-icon-control-end" />
              </PaginationLink>
            </PaginationItem>


          </Pagination>
          {
            showPageSizeOptions &&
            <div className="float-right pt-2">
              <span className="text-muted text-small mr-1">Items </span>
              <UncontrolledDropdown className="d-inline-block">
                <DropdownToggle caret color="outline-primary" size="xs">
                  {this.state.pageSize}
                </DropdownToggle>
                <DropdownMenu right>
                  {pageSizeOptions.map((size, index) => {
                    return (
                      <DropdownItem
                        key={index}
                        onClick={() => this.changePageSize(size)}
                      >
                        {size}
                      </DropdownItem>
                    );
                  })}
                </DropdownMenu>
              </UncontrolledDropdown>
            </div>
          }
        </div>
      </Fragment>
    );
  }
}
